let active_w = 375;
let active_s = 100;
let new_w = document.documentElement.clientWidth
document.documentElement.style.fontSize = (active_s * new_w / active_w)+'px';
window.onresize=function(){
    let new_w = document.documentElement.clientWidth
    document.documentElement.style.fontSize = (active_s * new_w / active_w)+'px'
}