<template>
  <div class="home">
    <div class="tou">
      <div
        v-for="(v, i) in sz"
        :key="'a' + i"
        :class="{ acti: v.zt == ($route.query.tab?$route.query.tab:'all') }"
        @click="diancli(i)"
      >
        {{ v.name }}
      </div>
    </div>

    <div class="di" v-if="zf">
      <div v-for="(v,i) in ssz" :key="'b'+i" @click="clixq(v.id)">
        <div>{{v.title}}</div>
        <p>{{_dayjs(v.last_reply_at).locale("zh-cn").fromNow()}}</p>
      </div>
    </div>
    <div class="dia" v-else>暂无数据</div>
  </div>
</template>

<script>
import dayjs from "dayjs";
import relativeTime from "dayjs/plugin/relativeTime";
import "dayjs/locale/zh-cn";
dayjs.extend(relativeTime);
export default {
  name: "Home",
  data() {
    return {
      sz: [
        { name: "全部", zt: "all" },
        { name: "精选", zt: "good" },
        { name: "分享", zt: "share" },
        { name: "问答", zt: "ask" },
        { name: "招聘", zt: "job" },
      ], 
      ssz: [],
      _dayjs:'',
      zf:true,
    };
  },
  created() {

    let a =0;
    if(this.$route.query.tab){
      this.sz.forEach(item => {
      if(item.zt!==this.$route.query.tab){
        a++;
      } 
    });
    }else{
      
      a = 0;
    }

    if(a==this.sz.length){
      this.zf=false;
    }else{
      this.zf=true;
    }

    this.hq(this.$route.query.tab?this.$route.query.tab:'all');
    this._dayjs = dayjs; 
  },
  methods: {
    diancli(index) { 
    if(this.$route.query.tab==this.sz[index].zt){return;}
      this.zf=true;
      this.hq(this.sz[index].zt).then(()=>{
        this.$router.push({
        name:'Home',
        query:{
          tab:this.sz[index].zt
        }
      })
      }); 
    },

    hq(tab) {  
     return axios.get("https://cnodejs.org/api/v1/topics?tab=" +tab).then((res) => {
        if ((res.status >= 200 && res.status < 300) || res.status == 304) {
          this.ssz = res.data.data;  
        }
      });
    },
    clixq(id){
      this.$router.push({
        name:'About',
        params:{
          id:id
        }
      })
    }
  },
};
</script>
<style scoped>
.tou {
  display: flex;
  justify-content: space-around;
  line-height: 0.4rem;
  font-size: 0.14rem;
}
.tou > div {
  width: 20%;
  text-align: center;
}
.tou > .acti {
  background: cyan;
}
.di {
  width: 100%;
  line-height: 0.4rem;
  font-size: 0.14rem;
}
.di > div {
  width: 100%;
  display: flex;
  justify-content: space-around;
  text-align: center;
}
.di > div > div {
  width: 80%;
  overflow: hidden; 
  white-space: nowrap; 
  text-overflow: ellipsis; 
  -o-text-overflow: ellipsis;
}
.di > div > p {
  width: 20%;
  white-space: nowrap; 
  text-overflow: ellipsis; 
  -o-text-overflow: ellipsis;
}
.dia{
  font-size:0.14rem;
}
</style>
