<template>
  <div id="app"> 
    <router-view/>
    <div class="ding" v-show="dinga" @click="ding">顶</div>
  </div>
</template>
<script> 
export default {
  name:'App',
  data(){
    return {
      dinga:false
    }
  }, 
  mounted(){
    window.onscroll =  ()=> {
  //为了保证兼容性，这里取两个值，哪个有值取哪一个
  //scrollTop就是触发滚轮事件时滚轮的高度
  var scrollTop = document.documentElement.scrollTop || document.body.scrollTop; 
  if(scrollTop>=300){
    this.dinga=true;
  }else{
    this.dinga=false;
  }
} 
  },
  methods:{
    ding(){
      let timer = false;
       clearInterval(timer)
        var scrollHeight = document.body.offsetHeight - document.documentElement.clientHeight;
        timer = setInterval(function() {
            var scrolltop = document.documentElement.scrollTop || document.body.scrollTop; 
                    window.scrollBy(0, -100);
                    if (Math.ceil(scrolltop) <= 0) {
                        clearInterval(timer)
                    };
        }, 50)

    }
  }
}
</script>

<style scoped>
  .ding{
    width: .4rem;
    height: .4rem;
    line-height: .4rem;
    text-align: center;
    position: fixed;
    bottom: .2rem;
    right: .2rem;
    font-size: .14rem;
    background: pink;
    border-radius: 1.8rem;
  }
</style>
